<template>
  <div class="today">
      <div class="top clearfix">
      <h1 class="large_t fl">今日推荐</h1>
      <span class="fr avatar"></span>
      </div>
      <!-- star product-->
      <div class="star">
        <h4 class="two_t_h">明星单品</h4>
        <div class="border">
            <swiper :list="star_list" class="banner" :loop="true" :show-desc-mask="false" dots-position="center" height="1.7rem"></swiper>            
        </div>
      </div>
      <!-- good product -->
      <div class="perfect clearfix">
        <h4 class="two_t_h">得乐良品<span class="fr three_t_m" @click="$router.push('/recommend')">更多商品 >></span></h4>
        <img src="../../assets/test/suannai1.png" @click="$router.push('/detail')" alt="" class="banner">
      </div>
      <!-- nearly dele -->
      <div class="position">
        <h4 class="two_t_h">附近的得乐 Café<span @click="$router.push('/home/nearby')" class="fr three_t_m">所有门店 >></span></h4>
        <div class="map clearfix">
          <div class="left fl">
              <h5 class="three_t_m">离你最近</h5>
              <h5 class="three_t_h store">深圳宝安中洲店</h5>
              <h5 class="three_t_m">创业二路与前进路交汇处</h5>
              <h5 class="three_t_h">营业至 23:00</h5>
          </div>
          <div class="right fr" id="map">

          </div>
        </div>
      </div>
      <!-- enterprise entrance -->
      <div class="enterprise">
        <h4 class="two_t_h">企业订购</h4>
        <img @click="$router.push('/personalCenter/enterpriserecommend')" src="../../assets/test/suannai1.png" alt="" class="banner">        
      </div>
    </div>
</template>

<script>
import { Swiper } from "vux";

export default {
  name: "Today",
  data() {
    return {
      star_list: [
        {
          url: "javascript:",
          img: require("../../assets/test/suannai.jpg"),
          title: ""
        },
        {
          url: "javascript:",
          img: require("../../assets/test/suannai1.png"),
          title: ""
        },
        {
          url: "javascript:",
          img: require("../../assets/test/suannai.jpg"),
          title: "",
          fallbackImg: "https://static.vux.li/demo/3.jpg" //img加载不出的时候作为替换
        }
      ]
    };
  },
  mounted() {
    this.mapInit();
  },
  methods: {
    mapInit() {
      var map = new BMap.Map("map"); // 创建地图实例
      var position_list = [
        {
          name: "深圳宝安中洲店",
          time: "营业至 13:00",
          address: "深圳市南山区tcl国际E城f1栋A座4楼"
        },
        {
          name: "深圳福田中洲店",
          time: "营业至 23:00",
          address: "深圳市南山区tcl国际E城f1栋A座4楼"
        },
        {
          name: "深圳龙华中洲店",
          time: "营业至 06:00",
          address: "深圳市南山区tcl国际E城f1栋A座4楼"
        }
      ];
      var point = new BMap.Point(113.933268, 22.577035); // 创建点坐标
      var point1 = new BMap.Point(113.9363382349, 22.5750183567); // 创建点坐标
      var point2 = new BMap.Point(113.935831, 22.575661); // 创建点坐标
      var point3 = new BMap.Point(113.9344714174, 22.5755632497); // 创建点坐标

      var a = +map.getDistance(point, point1).toFixed(0);
      var b = +map.getDistance(point, point2).toFixed(0);
      var c = +map.getDistance(point, point3).toFixed(0);

      var targetPosition;
      var arr = [a, b, c];
      var minPosition = arr.sort((a, b) => a - b)[0];
      if (a == minPosition) {
        console.log("a最短，位置是宝安店");
        targetPosition = point1;
      } else if (b == minPosition) {
        console.log("b最短，位置是福田店");
        targetPosition = point2;
      } else if (c == minPosition) {
        console.log("c最短，位置是龙华店");
        targetPosition = point3;
      }
      console.log(arr);
      console.log(arr.sort((a, b) => a - b)[0]);

      map.centerAndZoom(point, 50); // 初始化地图，设置中心点坐标和地图级别
      // map.enableScrollWheelZoom(true); //开启鼠标滚轮缩放
      map.disableDragging();
      var marker = new BMap.Marker(point);
      map.addOverlay(marker);
      var marker1 = new BMap.Marker(point1);
      map.addOverlay(marker1);
      var marker2 = new BMap.Marker(point2);
      map.addOverlay(marker2);
      var marker3 = new BMap.Marker(point3);
      map.addOverlay(marker3);
      var label = new BMap.Label("深圳市沃菲数字营销", {
        offset: new BMap.Size(-30, -30)
      });
      marker.setLabel(label);
      var label1 = new BMap.Label("深圳宝安中洲店", {
        offset: new BMap.Size(-30, -30)
      });
      marker1.setLabel(label1);
      var label2 = new BMap.Label("深圳福田益田店", {
        offset: new BMap.Size(-30, -30)
      });
      marker2.setLabel(label2);
      var label3 = new BMap.Label("深圳南山西丽店", {
        offset: new BMap.Size(-50, -30)
      });
      label3.setStyle({
        color: "#000",
        fontSize: "0.16rem",
        backgroundColor: "0.05",
        border: "0",
        fontWeight: "bold"
      });
      marker3.setLabel(label3);
      // map.enableScrollWheelZoom(true);
      setTimeout(function() {
        map.panTo(targetPosition);
      }, 2000);
    }
  },
  components: {
    Swiper
  }
};
</script>

<style scoped lang="less">
// 动画效果

.banner {
  width: 3.2rem;
  height: 1.7rem;
  display: block;
  margin-top: 0.05rem;
  border-radius: 0.12rem;
  font-size: 0.16rem;
  margin-bottom: 0.3rem;
}
.vux-slider > .vux-swiper > .vux-swiper-item > a {
  border-radius: 0.12rem;
  overflow: hidden;
}
// 顶栏
.top {
  padding-bottom: 0.15rem;
  .avatar {
    width: 0.3rem;
    height: 0.3rem;
    border-radius: 50%;
    background-image: url("../../assets/avatar.jpg");
    background-size: cover;
    margin-top: 0.09rem;
  }
}
// 明星单品
// 得乐良品
.perfect {
  height: 2.27rem;
  width: 3.2rem;
}
.position {
  .map {
    width: 3.2rem;
    height: 1.2rem;
    border: 0.01rem solid rgba(0, 0, 0, 0.2);
    border-radius: 0.12rem;
    margin-top: 0.05rem;
    overflow: hidden;
    .left {
      padding: 0.15rem 0 0 0.15rem;
      width: 50%;
      height: 1.2rem;
      .store {
        margin-bottom: 0.2rem;
      }
    }
    .right {
      width: 50%;
      height: 1.2rem;
      border-radius: 0 0.12rem 0.14rem 0;
      overflow: hidden;
    }
  }
}
.enterprise {
  margin-top: 0.3rem;
}
</style>



