
import Swiper from 'vux/src/components/swiper/swiper.vue'


export default {
name: "Today",
data() {
return {
star_list: [
{
url: "javascript:",
img: require("../../assets/test/suannai.jpg"),
title: ""
},
{
url: "javascript:",
img: require("../../assets/test/suannai1.png"),
title: ""
},
{
url: "javascript:",
img: require("../../assets/test/suannai.jpg"),
title: "",
fallbackImg: "https://static.vux.li/demo/3.jpg" 
}
]
};
},
mounted() {
this.mapInit();
},
methods: {
mapInit() {
var map = new BMap.Map("map"); 
var position_list = [
{
name: "深圳宝安中洲店",
time: "营业至 13:00",
address: "深圳市南山区tcl国际E城f1栋A座4楼"
},
{
name: "深圳福田中洲店",
time: "营业至 23:00",
address: "深圳市南山区tcl国际E城f1栋A座4楼"
},
{
name: "深圳龙华中洲店",
time: "营业至 06:00",
address: "深圳市南山区tcl国际E城f1栋A座4楼"
}
];
var point = new BMap.Point(113.933268, 22.577035); 
var point1 = new BMap.Point(113.9363382349, 22.5750183567); 
var point2 = new BMap.Point(113.935831, 22.575661); 
var point3 = new BMap.Point(113.9344714174, 22.5755632497); 

var a = +map.getDistance(point, point1).toFixed(0);
var b = +map.getDistance(point, point2).toFixed(0);
var c = +map.getDistance(point, point3).toFixed(0);

var targetPosition;
var arr = [a, b, c];
var minPosition = arr.sort((a, b) => a - b)[0];
if (a == minPosition) {
console.log("a最短，位置是宝安店");
targetPosition = point1;
} else if (b == minPosition) {
console.log("b最短，位置是福田店");
targetPosition = point2;
} else if (c == minPosition) {
console.log("c最短，位置是龙华店");
targetPosition = point3;
}
console.log(arr);
console.log(arr.sort((a, b) => a - b)[0]);

map.centerAndZoom(point, 50); 

map.disableDragging();
var marker = new BMap.Marker(point);
map.addOverlay(marker);
var marker1 = new BMap.Marker(point1);
map.addOverlay(marker1);
var marker2 = new BMap.Marker(point2);
map.addOverlay(marker2);
var marker3 = new BMap.Marker(point3);
map.addOverlay(marker3);
var label = new BMap.Label("深圳市沃菲数字营销", {
offset: new BMap.Size(-30, -30)
});
marker.setLabel(label);
var label1 = new BMap.Label("深圳宝安中洲店", {
offset: new BMap.Size(-30, -30)
});
marker1.setLabel(label1);
var label2 = new BMap.Label("深圳福田益田店", {
offset: new BMap.Size(-30, -30)
});
marker2.setLabel(label2);
var label3 = new BMap.Label("深圳南山西丽店", {
offset: new BMap.Size(-50, -30)
});
label3.setStyle({
color: "#000",
fontSize: "0.16rem",
backgroundColor: "0.05",
border: "0",
fontWeight: "bold"
});
marker3.setLabel(label3);

setTimeout(function() {
map.panTo(targetPosition);
}, 2000);
}
},
components: {
Swiper
}
};



